body {
  font-family: 'Segoe UI', sans-serif;
  background-color: #f4f6fa;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 900px;
  margin: 40px auto;
  padding: 30px;
  background-color: white;
  border-radius: 25px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
}

h1 {
  text-align: center;
  color: #4b5563;
  margin-bottom: 30px;
}

.question-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}

.question-row {
  display: grid;
  grid-template-columns: 2fr 1fr;
  align-items: center;
  gap: 20px;
}

.question {
  font-weight: 500;
  color: #374151;
}

.slider input {
  width: 100%;
}

.slider span {
  display: inline-block;
  margin-top: 5px;
  font-size: 14px;
}

.results-box {
  margin-top: 40px;
  background: #f0f4ff;
  padding: 25px;
  border-radius: 20px;
  box-shadow: inset 0 0 5px #d3e0f3;
}

.results-box h2 {
  margin-bottom: 10px;
  color: #1f2937;
}

.interpret {
  margin-top: 10px;
  font-style: italic;
  color: #333;
}

.tip {
  margin-top: 10px;
  font-weight: bold;
  color: #2563eb;
}

.canvas {
  margin-top: 20px;
  text-align: center;
}
